import { Component, Inject, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { FileToStorage } from 'src/app/models/file-to-storage';
import { Filetype } from 'src/app/models/filetype';
import { CommissionService } from 'src/app/services/commission.service';
import { environment } from 'src/environments/environment';
import { ModalInfoComponent } from '../modal-info/modal-info.component';
import { ModalquestionComponent } from '../modalquestion/modalquestion.component';

@Component({
  selector: 'app-modal-upload',
  templateUrl: './modal-upload.component.html',
  styleUrls: ['./modal-upload.component.css']
})
export class ModalUploadComponent implements OnInit {
  breakpoint: number;
  isMobile = true;
  isCreatingChart: boolean;
  isLoading = false;
  isErrorLoadFile = false;
  currentWeigth = 0;
  img: File;
  files: File[] = [] as File[];
  inputFiles: any;
  maxWeigth = environment.maxWeigthFile;
  lisfiletype: Filetype[] = [] as Filetype[];
  maxFiles = 2;
  Extpermitida: string;
  Extfile: string;
  isExtElectrico: boolean = false;
  selectValue: any;
  tray: any;
  numberbyte: number = 1024;
  isbutton: boolean = false;
  registerForm = new FormGroup({
    files: new FormControl(''),
    tipoestado: new FormControl('', [Validators.required]),
  });

  constructor(
    private snackBar: MatSnackBar,
    public dialogRef: MatDialogRef<ModalUploadComponent>,
    public dialog: MatDialog,
    private commissionService: CommissionService,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) {
    this.tray = this.data;
    this.isMobile = this.data.isMobile;
  }

  removeFile(i) {
    this.currentWeigth -= this.files[i].size / this.numberbyte;
    this.files.splice(i, 1);
  }

  onChange(deviceValue) {
    this.selectValue = deviceValue;
    if (this.selectValue == '1') {
      this.Extpermitida = ' .xml ';
      this.Extfile = ' xml ';
      this.isExtElectrico = true;
    } else {
      this.Extpermitida = ' .jpg, .png, .pdf ';
      this.Extfile = '  ';
      this.isExtElectrico = false;
    }
    this.isbutton = true;
    this.files = [] as File[];
    this.currentWeigth = 0;
  }

  onFileSelected(event) {
    var extension = (this.inputFiles.substr(this.inputFiles.lastIndexOf("."))).toLowerCase();
    if (extension === ".pdf" || extension === ".jpeg" || extension === ".png" || extension === ".jpg" || extension === ".xml") {
      if ((this.files.length == this.maxFiles && this.isExtElectrico)
        || (this.files.length == 1 && !this.isExtElectrico)) {
        this.snackBar.open('Superó el máximo de archivos permitidos', 'OK', {
          duration: 5000,
        });
        return;
      } else {
        this.currentWeigth = 0;
        for (let index = 0; index < event.target.files.length; index++) {
          this.files.push(event.target.files[index]);
          if (index >= (event.target.files.length - 1)) {
            this.inputFiles = '';
            for (let indexTwo = 0; indexTwo < this.files.length; indexTwo++) {
              this.currentWeigth += this.files[indexTwo].size / this.numberbyte;
              if ((this.files[indexTwo].size / this.numberbyte) > this.maxWeigth) {
                this.removeFile(indexTwo);
                this.snackBar.open('Superó el máximo de tamaño permitido(' + this.maxWeigth / this.numberbyte + 'MB por archivo) para el documento actual', 'OK', {
                  duration: 5000,
                });
              } else {
                if (this.selectValue == '1') {
                  this.Extfile = ' pdf ';
                } else {
                  this.Extfile = '  ';
                }
              }
            }
          }
        }
      }
    }
  }

  onNoClick(): void {
    this.dialogRef.close(false);
  }
  onSave() {
    var typevoucher = ' '
    if (this.selectValue == '1') {
      typevoucher = ' de pago electrónico'
    }
    if (this.files.length <= 0) {
      this.snackBar.open('Seleccione un archivo', 'OK', {
        duration: 5000,
      });
      return;
    }
    const dialogref = this.dialog.open(ModalquestionComponent, {
      data: {
        message: "¿Está seguro de cargar el comprobante" + typevoucher + " ?",
        type: "generic",
      },
    });
    dialogref.afterClosed().subscribe((response) => {
      if (response === true) {
        this.sendFile();
      }
    });
  }

  async sendFile() {
    this.isLoading = true;
    var formData = new FormData();
    this.files.forEach(file => {
      formData.append('file', file, file.name);
    });

    this.commissionService.postfile(this.registerForm.controls.tipoestado.value, this.tray.obj, formData).subscribe(
      async data => {
        this.isLoading = false;
        if (data.success) {
          this.openDialogInfo("", data.message, "", "success");
          this.dialogRef.close(1);
        } else {
          if (data.validationErrors != null) {
            this.openDialogInfo(
              "Proceso erroneo !",
              data.validationErrors, "",
              "listError"
            );
          } else {
            if (!data.data) {
              this.openDialogInfo("", data.message, "", "error");
            } else {
              var auxs = '';
              if (this.selectValue == '1' && data.message.indexOf('SUNAT') == -1) {
                auxs = 'Favor volver a generar otro comprobante de pago electrónico';
              }
              if (data.data.find((x: any) => x.includes('fuera del cierre fiscal'))) {
                auxs = '';
              }
              if (data.message.includes('por favor intente en unos minutos')) {
                auxs = '';
              }
              this.openDialogInfo(data.message, Object.values(data.data), auxs, 'listError');
            }
          }
        }
      },
      err => {
        this.isLoading = false;
        this.dialogRef.close();
        console.log(err);
      }
    );
  }



  openDialogInfo(title: string, subtitle: any, footer: string, type: string) {
    const dialogref = this.dialog.open(ModalInfoComponent, {
      width: '450px',
      data: { title, subtitle, footer, type }
    });
    dialogref.afterClosed().subscribe(reponse => {
    });
  }

  ngOnInit() {
    this.getAllApplication();
  }

  getAllApplication() {
    this.commissionService.getfiletypes().subscribe(
      async (data) => {
        this.lisfiletype = data.data;
      },
      (err) => {
        console.log(err);
      }
    );
  }
}
