import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { MatDialog } from '@angular/material/dialog';
import { AuthenticationService } from 'src/app/services/authentication.service';
import { ModalInfoComponent } from 'src/app/components/modal-info/modal-info.component';
import { AppService } from 'src/app/services/app.service';

@Component({
  selector: 'app-auth-callback',
  templateUrl: './auth-callback.component.html',
  styleUrls: ['./auth-callback.component.css']
})
export class AuthCallbackComponent implements OnInit {

  constructor(
    public dialog: MatDialog,
    public router: Router,
    private authService: AuthenticationService,
    private appService: AppService,
    ) { }

  ngOnInit() {
    console.info("AuthCallback");
    this.getIpAndStartApp();
  }

async getIpAndStartApp() {
  await this.authService.getUser();
  this.appService.getIPAddress().subscribe(
    res => {
      this.appService.setIpStorage(res.ip);
      this.authService.completeAuthentication();
    },
    err => {
      this.openDialogInfo(
        'Importante !',
        'Si tiene activa alguna extensión de bloqueo de publicidad desactívelo para poder navegar. Gracias.',
        'info'
      );
      console.log(err);
    }
  );
}

  openDialogInfo(title: string, subtitle: string, type: string) {
    const dialogref = this.dialog.open(ModalInfoComponent, {
      width: '350px',
      data: { title, subtitle, type }
    });

    dialogref.afterClosed().subscribe(reponse => {
    });
  }

}
