import { Component, Inject, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ModalInfoComponent } from 'src/app/components/modal-info/modal-info.component';
import { ComisionI } from 'src/app/models/Comision';
import { CommissionService } from 'src/app/services/commission.service';

@Component({
  selector: 'app-payrolldetail',
  templateUrl: './payrolldetail.component.html',
  styleUrls: ['./payrolldetail.component.css']
})
export class PayrolldetailComponent implements OnInit {
	storageUrl: string = '';
	dataComision: ComisionI = {} as ComisionI;

  constructor(
		public dialogRef: MatDialogRef<PayrolldetailComponent>,
		public dialog: MatDialog,
	   private commissionService: CommissionService,
		@Inject(MAT_DIALOG_DATA) public data: any,
    ) { 
	}

	ngOnInit() {
		this.getDetail(this.data.obj);
		this.getFile(this.data.obj);
  	}

	onVerdocumento(){
		this.openDialogInfo("Revisar documento de facturación","<div class='auto' align='center'><img class='auto' src='assets/images/Captura.PNG'></div>" , "");
	}

	onNoClick() {
		this.dialogRef.close();
	}

	getDetail(id) {
		this.commissionService.gettray(id).subscribe(
			async (data) => {
				if(data.data !== null){
					this.dataComision = data.data;
				}else{
					this.dataComision = {} as ComisionI;
				}
			},
			(err) => {
				console.log(err);
			}
		);
	}

	getFile(id) {
		this.commissionService.getfile(id).subscribe(
			async (data) => {
				if(data.data !== null){
					for (let index = 0; index < data.data.length; index++) {
						const element = data.data[index];
						if(element.contentType == 'application/pdf' || element.contentType.includes('image')){
							this.storageUrl = element.storageUrl;
							return;
						}
					}
				}else{
					this.storageUrl = '';
					return;
				}
			},
			(err) => {
				console.log(err);
			}
		);
	}

	openDialogInfo(title: string, subtitle: any, type: string) {
		const dialogref = this.dialog.open(ModalInfoComponent, {
			width: '350px',
			data: { title, subtitle, type }
		});
		dialogref.afterClosed().subscribe(reponse => {
		});
	}

}
