import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class CommissionService {

  constructor(public router: Router, private http: HttpClient) { }

	getperiods() {
		return this.http.get<any>(
		environment.invoceApi +  'master/periods',
		{},
		);
	}

	getfiletypes() {
		return this.http.get<any>(
		environment.invoceApi +  'master/filetypes',
		{},
		);
	}

  	getAlltray(Period, Status,pages,sizes) {
		let params = new HttpParams();
		params = params.append('PageNumber', pages);
		params = params.append('PageSize', sizes);
		if (Period !== '') {
			params = params.append('Period', Period);
		}
		if (Status !== '') {
			params = params.append('StatusId', Status);
		}
		return this.http.get<any>(
			environment.invoceApi + 'tray?', { params: params }
		);
	}

  	gettray(id) {
		return this.http.get<any>(
			environment.invoceApi + 'tray/'+ id,
		);
	}

	postfile(type, id, obj) {
		return this.http.post<any>(
			environment.invoceApi + 'file?fileType=' + type + '&trayId=' + id,
			obj
		);
	}

	getfile(id) {
	  return this.http.get<any>(
		  environment.invoceApi + 'file/'+ id,
	  );
  }

}
