import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { ModalInfoComponent } from 'src/app/components/modal-info/modal-info.component';

@Component({
  selector: 'app-store',
  templateUrl: './store.component.html',
  styleUrls: ['./store.component.css']
})
export class StoreComponent implements OnInit {

  currentCommerce: any;
  currentStore: any;

  data = {
    title: 'Elige una tienda',
    items: [
      {
        id: 0,
        title: 'Oechsle Lima',
      },
      {
        id: 1,
        title: 'Oechsle Trujillo',
      },
      {
        id: 2,
        title: 'Oechsle Piura',
      },
      {
        id: 3,
        title: 'Oechsle Arequipa',
      },
    ]
  }

  constructor(
    public router: Router,public dialog: MatDialog, private activatedRoute: ActivatedRoute
  ) { }

  ngOnInit(): void {
    this.activatedRoute.queryParams.subscribe(params => {
      const obj = {
        id: params.idCommerce,
        title: params.titleCommerce
      };
      this.currentCommerce = obj;
    });
  }

  selectItem(item: any){
    console.log(item);
    this.openDialogInfo(
      'Tienda seleccionada',
      'Has elegido el Comercio ' + this.currentCommerce.title + ' y la Tienda ' + item.title + '.',
      '',
      'info'
    );
    localStorage.setItem('idStore', item.id);
    localStorage.setItem('titleStore', item.title);
    localStorage.setItem('idCommerce', this.currentCommerce.id);
    localStorage.setItem('titleCommerce', this.currentCommerce.title);
  }

  openDialogInfo(title: string, subtitle: any, footer: string, type: string) {
    const dialogref = this.dialog.open(ModalInfoComponent, {
      // width: '450px',
      data: { title, subtitle, footer, type },

    });
    dialogref.afterClosed().subscribe(reponse => {
      this.router.navigate(['/dashboard/home']);
    });
  }

}
