import { Component, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

@Component({
  selector: 'app-modalquestion',
  templateUrl: './modalquestion.component.html',
  styleUrls: ['./modalquestion.component.css']
})
export class ModalquestionComponent implements OnInit {

  constructor(
    public dialogRef: MatDialogRef<ModalquestionComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    ) {
      dialogRef.disableClose = true;
  }

  close() {
    this.dialogRef.close(false);
  }

  onSave() {
    this.dialogRef.close(true);
	}

  ngOnInit() {
  }

}
