import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { SidenavComponent } from 'src/app/components/sidenav/sidenav.component';
import { AuthCallbackComponent } from 'src/app/pages/auth-callback/auth-callback.component';
import { CommerceComponent } from 'src/app/pages/commerce/commerce.component';
import { CommissionComponent } from 'src/app/pages/commission/commission.component';
import { HomeComponent } from 'src/app/pages/home/home.component';
import { OperationsComponent } from 'src/app/pages/operations/operations.component';
import { StoreComponent } from 'src/app/pages/store/store.component';
import { TerminalsComponent } from 'src/app/pages/terminals/terminals.component';
import { AppService } from 'src/app/services/app.service';
import { AuthorizationService } from 'src/app/services/authorization.service';


const routes: Routes = [
  {
    path: '',
    component: SidenavComponent,
    children: [
      {
        path: '',
        redirectTo: 'bienvenido',
      },
      {
        path: 'bienvenido',
        component: HomeComponent
      },
      {
        path: 'comercio',
        component: CommerceComponent
      },
      {
        path: 'tienda',
        component: StoreComponent
      },
      {
        path: 'terminales',
        component: TerminalsComponent
      },
      {
        path: 'operaciones',
        component: OperationsComponent
      },
      {
        path: 'auth-callback',
        component: AuthCallbackComponent
      },
      {
        path: 'auth-callback',
        component: AuthCallbackComponent
      },
      {
        path: 'documentos-y-pagos',
        component: CommissionComponent,
        canActivate: [AuthorizationService, AppService],
      },
    ],
  },
  {
    path: '',
    redirectTo: '',
    pathMatch: 'full'
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class InAppRoutingModule { }
