import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ModalUploadComponent } from 'src/app/components/modal-upload/modal-upload.component';
import { LisComisionI } from 'src/app/models/Comision';
import { PaginateI } from 'src/app/models/filter';
import { Periods } from 'src/app/models/periods';
import { CommissionService } from 'src/app/services/commission.service';
import { environment } from 'src/environments/environment';
import { PayrolldetailComponent } from '../payrolldetail/payrolldetail.component';

@Component({
  selector: 'app-commission',
  templateUrl: './commission.component.html',
  styleUrls: ['./commission.component.css']
})
export class CommissionComponent implements OnInit {
  dataChildren: any;
  records: number;
  isMobile = true;
  isCreatingChart: boolean;
  filterIsShow = false;
  filterFormGroup = new FormGroup({
    tipoPeriodo: new FormControl('', [Validators.maxLength(200)]),
    tipoestado: new FormControl('', [Validators.maxLength(200)]),
  });
  dataSource: LisComisionI[] = [] as LisComisionI[];
  dataSelect: LisComisionI = {} as LisComisionI;
  lisperiod: Periods[] = [] as Periods[];
  displayedColumnsFullDesktop = ["period", 'accountNumber', 'documentType', "totalCommission", "processName", "statusName", "option",];
  displayedColumnsFullmovil = ["period", "totalCommission", "statusName", "option",];
  constructor(
    public dialog: MatDialog,
    public router: Router,
    private commissionService: CommissionService
  ) {
    this.isMobile = (window.innerWidth <= 750) ? true : false;
  }

  ngOnInit() {
    this.isMobile = (window.innerWidth <= 750) ? true : false;
    this.initAndLoad();
  }


  onResize(event) {
    this.isMobile = (window.innerWidth <= 750) ? true : false;
  }

  collapseChange() {
    this.filterIsShow = !this.filterIsShow;
  }

  initAndLoad() {
    this.filterList();
    this.getAllApplication();
  }

  filterList() {
    this.getAll({ pages: 1, sizes: environment.paginate } as PaginateI)
  }

  getAll(obj: PaginateI) {
    var tipoPeriodo = this.filterFormGroup.controls.tipoPeriodo.value == null ? '' : this.filterFormGroup.controls.tipoPeriodo.value;
    this.commissionService.getAlltray(tipoPeriodo, '', obj.pages, obj.sizes).subscribe(
      async (data) => {
        this.dataChildren = data.data;
        this.dataSource = this.dataChildren.items;
        this.records = this.dataChildren.totalCount;
      },
      (err) => {
        if (err.status === 401 || err.status === 403) {
        }
        console.log("config Error :" + err);
      }
    );
  }

  getAllApplication() {
    this.commissionService.getperiods().subscribe(
      async (data) => {
        this.lisperiod = data.data;
      },
      (err) => {
        if (err.status === 401 || err.status === 403) {
        }
        console.log(err);
      }
    );
  }

  onlyNumberKey(event) {
    return event.charCode >= 48 && event.charCode <= 57;
  }

  openDialogUploadCheckDocument(itemtSelect: LisComisionI) {
    this.dataSelect = itemtSelect;
    const dialogref = this.dialog.open(ModalUploadComponent, {
      disableClose: true,
      width: (this.isMobile) ? '100%' : '650px',
      data: {
        title: 'Carga de comprobante de pago',
        subtitle: ' ',
        obj: this.dataSelect.id,
      }
    });
    dialogref.afterClosed().subscribe(response => {
      if ((response !== false)) {
        this.filterList();
      }
    });
  }

  openDialogpayrolldetail(itemtSelect: LisComisionI) {
    this.dataSelect = itemtSelect;
    const dialogref = this.dialog.open(PayrolldetailComponent, {
      disableClose: true,
      width: (this.isMobile) ? '100%' : '70%',
      data: {
        title: '',
        subtitle: ' ',
        obj: this.dataSelect.id,
      }
    });
    dialogref.afterClosed().subscribe(response => {
      if ((response !== false)) {
        console.info('response :' + response);
      }
    });
  }

}
