
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute, ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
import { ModalInfoComponent } from '../components/modal-info/modal-info.component';
import { AuthenticationService } from './authentication.service';

@Injectable({
  providedIn: 'root'
})
export class AuthorizationService implements CanActivate {
	roleList = [];
	constructor(
		public dialog: MatDialog,
		private authService: AuthenticationService,
		public router: Router,
		private http: HttpClient,
		private activatedRoute: ActivatedRoute
	) {
	}


	canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<boolean> | Promise<boolean> | boolean {
		return this.authService.getRolByApplication(environment.idApp).pipe(map(data => {
			if (data.isSuccess) {
				this.roleList = data.data;
				var rolpermitido= '';
				for (let index = 0; index < this.roleList.length; index++) {
					const element = this.roleList[index];
					let rolwp = element.indexOf(".");
					if(rolwp == -1){
						rolpermitido =element.toUpperCase();
					} else{
						var roleSeparated = element.split(".");
						rolpermitido = roleSeparated[0].toUpperCase();
					}
					if (rolpermitido == 'AGENT') {
						return true;
					}
				}
				this.openDialogInfo("Error en credenciales !", "Tu usuario no tiene permisos en esta aplicación", "error");
				this.authService.logout();
			} else {
				this.openDialogInfo('Sesión caducada  !','Vuelva a iniciar sesión','error');
				this.authService.logout();
			}
				return false;
		},(err) => {
			console.log(err);
			this.authService.logout();
			this.openDialogInfo("","Ocurrió un error. Intentelo mas tarde","error");
			return false;
			}
		))
	}

	openDialogInfo(title: string, subtitle: any, type: string) {
		const dialogref = this.dialog.open(ModalInfoComponent, {
			disableClose: true,
			width: '350px',
			data: { title, subtitle, type },
		});
	}

}
